p{
  color: white;
  white-space: pre-wrap;
}

.Name{
    margin-top: 30px;
    margin-left: 50px;
    margin-right: 50px;
    text-align: left;
    font-weight: bold;
    font-size: 20px;
    border-bottom: 1px solid #eee;
}

.Github{
  display: flex;
  text-align: left;
  width: 50px;
}

.Description{
    word-break: break-all;
    text-align: left;
    margin-left: 52px;
    font-size: 20px;
    font-weight: bold;
}

.ContentWrapper {
    display: flex;   
}

.Content{
    text-align: left;
    margin-left: 52px;
    margin-right: 700px;
    font-size: 15px;
}

.Face{
  margin-right: 300px;
  margin-left: -500px;
}

.Education_Awards{
  display: flex;
}

.Education{
  margin-left: 50px;
}

.Awards{
  margin-left: 100px;
}

.ETitle{
  font-weight: bolder;
  font-size: 30px;
}

.E2Title{
  font-weight: bold;
  font-size: 20px;
}

@media (min-width: 0px) and (max-width: 1024px) {
  .Name,
  .Description,
  .Content,
  .Education,
  .Awards {
    margin: 0 20px;
    font-size: 14px;
  }
  .Face{
    margin: 0;
  }
  .Education_Awards{
    display: block;
  }
}